import {fabric} from 'fabric'
class shapes{
    static createSkiiCircle({
        radius, fill, left, top, name, stroke
    }){
        return new fabric.Circle({
            radius:radius,
            fill: fill,
            left: left,
            top: top,
            name: name,
            stroke: stroke
        })
    }

    static createSkiiRect({
        left,
        top,
        fill,
        width,
        height,
        name,
        stroke
    }){
            return new fabric.Rect({
                left: left,
                top:top,
                fill: fill,
                width:width,
                height:height,
                name: name,
                stroke: stroke
            });
        }
}

export{
    shapes
}