import {atom} from 'jotai'
import {SkiiObject} from '../models/SkiiObject'

const selectedEntityAtom = atom<SkiiObject|null>(null)
export const deleteEntityAton = atom<string|null>(null)

export const setSelectedEntityAtom = atom(
    null,
    (get,set,update:SkiiObject|null) =>{
        set(selectedEntityAtom, ()=> update)
    }
)

export const activeEntityFillColorAtom = atom<string|null>(null)
export const activeEntityStrokeColorAtom = atom<string|null>(null)
export const getSelectedEntityAtom = atom((get)=>get(selectedEntityAtom))