import React, {useState, useCallback}from "react";
import './sizeSelectionView.css';
import {useNavigate, NavigateFunction} from 'react-router-dom'
export interface Props {
    heading: string;
}

function SizeSelectionView(){
    const [height, setHeight] = useState<number>(1)
    const [width, setWidth] = useState<number>(1)
    const navigate: NavigateFunction = useNavigate()
    const goBtnClicked = useCallback(()=> navigate(`/draw?w=${width}&h=${height}`,{}), [])
    return(
        <div className="size-selection-view-container">
               <h3> Sketchii </h3>
               <div>
                   <input type="number" min="1" name="width" id="width"
                    value={width}
                    onChange = {(e)=>{
                        const nW:number = parseInt(e.target.value) || 1
                        setWidth(nW)
                    }}
                    />
                   <label className="label" htmlFor="width"> width</label>

                   <input type="number" min="1" name="height" id='height' 
                   value={height}
                   onChange ={(e)=>{
                        const nH:number = parseInt(e.target.value)|| 1
                        setHeight(nH)
                   }}
                   />
                   <label className="label" htmlFor="height">height</label>
               </div>
               <button className="primary-btn" onClick={goBtnClicked}>GO </button>
        </div>
    )
}

export {
    SizeSelectionView
}