import React,{useEffect} from "react";
import {createCanvas, options, CANVAS_COMMAND} from "../control/skiiCanvas";
import {getDrawCommandAtom, isDrawModeOnAtom, drawImageEncodedAtom, backgroundImageEncodedAtom} from '../stores/leftControlContainerStore'
import {setSelectedEntityAtom,deleteEntityAton, activeEntityFillColorAtom, activeEntityStrokeColorAtom} from '../stores/SkiiEntitiesStore'
import {SkiiObject} from '../models/SkiiObject'
import {useAtom} from 'jotai'
export interface CanvasProps {
    width:number,
    height:number
}

export const Canvas = (props:CanvasProps) => {
    const [,setSelectedEntity] = useAtom(setSelectedEntityAtom)
    const [backgroundImage,] = useAtom(backgroundImageEncodedAtom)
    const [toRemoveEntity,] = useAtom(deleteEntityAton)
    const [fillColor, ] = useAtom(activeEntityFillColorAtom)
    const opt: options = {
        width: props.width || 1080,
        height: props.height || 900,
        handleSelectedEntityEvents: (e) =>{
            if(e.selected){
                const skiiObject:SkiiObject = e.selected[0]
                setSelectedEntity(skiiObject)
            }
            else{
                setSelectedEntity(null)
            }
        },
        handleEntityChangeEvents:(e) =>{
            if(e){
                const skiiObject:SkiiObject = e
                setSelectedEntity(skiiObject)
            }
        }

    }
    
    const [drawCommand] = useAtom<CANVAS_COMMAND>(getDrawCommandAtom)
  
    const [isDrawModeOn, ] = useAtom(isDrawModeOnAtom)
    const [drawImageEncoded,] = useAtom(drawImageEncodedAtom)

    useEffect(() =>{
        const canvas = createCanvas(opt)
        canvas.performCommand(drawCommand)
    },[drawCommand])

    useEffect(() =>{
        const canvas = createCanvas(opt)
        canvas.setDrawMode(isDrawModeOn)
    },[isDrawModeOn])

    useEffect(()=>{
        const canvas = createCanvas(opt)
        canvas.addImage(drawImageEncoded as string, true)
    }, [drawImageEncoded])

    useEffect(()=>{
        const canvas = createCanvas(opt)
        if(backgroundImage) canvas.addBackgroundImage(backgroundImage as string)
    },[backgroundImage])

    useEffect(()=>{
        if(toRemoveEntity){
            const canvas = createCanvas(opt)
            canvas.deleteEntity(toRemoveEntity)
        }
    },[toRemoveEntity])

    useEffect(()=>{
        const canvas = createCanvas(opt)
        if(fillColor){
            canvas.setActiveEntityFill(fillColor)
        }
    },[fillColor])

    const [strokeColor,] = useAtom(activeEntityStrokeColorAtom);
    useEffect(()=>{
        const canvas = createCanvas(opt)
        if(strokeColor){
            canvas.setActiveEntityStroke(strokeColor)
        }
    },[strokeColor])


    return (
        <canvas id="c" />
    )
}