import {fabric} from 'fabric'
import {shapes} from '../shapes/shapes'
import ShortUniqueId from 'short-unique-id';

export interface options{
    width:number,
    height:number,
    handleSelectedEntityEvents: (evt: any) => void | null 
    handleEntityChangeEvents:(evt:any) => void | null
}

export enum CANVAS_COMMAND{
    CIRCLE,
    SQUARE,
    TRIANGLE,
    DRAW,
    NONE
}

let canvas: SkiiCanvas;

class SkiiCanvas {

    c:fabric.Canvas
    uniqueId: ShortUniqueId

    constructor(options:options){
        this.c= new fabric.Canvas('c',{ 
            backgroundColor: 'rgb(248, 249, 250)',
            width: options.width,
            height: options.height
        })
        this.uniqueId = new ShortUniqueId({length: 4});

        this.setSelectedEntityHandler_(options.handleSelectedEntityEvents);
        this.setEntityChangedHandler_(options.handleEntityChangeEvents);
        
    }
    
    performCommand(cmd: CANVAS_COMMAND){
        switch(cmd){
            case CANVAS_COMMAND.CIRCLE:
                this.addCircle()
                break
            
            case CANVAS_COMMAND.SQUARE:
                this.addRectangle()
                break

            case CANVAS_COMMAND.DRAW:
                break
        }
    }

    setDrawMode(value: boolean){
        this.c.isDrawingMode = value;
    }
    
    addRectangle():void{
        this.c.add(shapes.createSkiiRect({
            left:100,
            top:100,
            fill:'green',
            width:20,
            height:20, 
            name: this.uniqueId(), 
            stroke: "#000000"
        }));
    }

    addCircle(){
        this.c.add(shapes.createSkiiCircle({
            radius:5,
            fill:'#00ff00',
            stroke:"#000000",
            left: 100,
            top:100,
            name: this.uniqueId()
        }))
    }

    /**
     * add an image to the canvas
     * @param image {@link string} encoded date of an image
     * @param scaleToFitScreen {@link boolean} true to make the image fit the screen. if false the original height and widht of the image is used
     */
    addImage(image:string, scaleToFitScreen: boolean = false){
        if(image){
            debugger
            fabric.Image.fromURL(image, (cImg)=>{
                let scaleX, scaleY = 1
                if(scaleToFitScreen &&
                     (cImg.width && cImg.height && this.c.width && this.c.height)
                     && (cImg.width > this.c.width || cImg.height > this.c.height)
                ){
                    scaleX = this.c.width / cImg.width
                    scaleY = this.c.width / cImg.width
                    
                    if(scaleX > 0.5) scaleX *= (1/8);
                    if(scaleY > 0.5) scaleY *= (1/8);
                    
                    cImg.scaleX = scaleX
                    cImg.scaleY = scaleY
                    cImg.name = this.uniqueId()
                }
                this.c.add(cImg)
            })

        }
    }

    /**
     * Sets the background image of the canvas
     * @param image {@link string} encoded date of an image
     */
    addBackgroundImage(image:string){
        if(image){
            fabric.Image.fromURL(image, (cImg)=>{
                //dWidth: destination width, dHeight: destination height
                //sWidth: source width, sHeight: source height
                const dWidth = this.c.getWidth(), dHeight = this.c.getHeight();
                const sWidth = cImg.getBoundingRect().width, sHeight = cImg.getBoundingRect().height;
                const scaleX = dWidth /sWidth, scaleY = dHeight /sHeight;
                cImg.scaleX = scaleX;
                cImg.scaleY = scaleY;
                cImg.name = this.uniqueId();
                this.c.add(cImg)
            })
        }
    }

    /**
     * set a function that would be called when there is a change in the selection object
     * @param handler function that gets called when selection object gets created, changed, cleared
     */
    setSelectedEntityHandler_(handler:(e:any)=> void|null){
        this.c.on('selection:cleared', handler)
        this.c.on('selection:created', handler)
        this.c.on('selection:updated', handler)
    }

    setEntityChangedHandler_(handler:(e:any)=> void|null){
        this.c.on('object:moving',(e)=>handler(e.target))
        this.c.on('object:scaling',(e)=>handler(e.target))
        this.c.on('object:rotating',(e)=>handler(e.target))
        this.c.on('object:skewing',(e)=>handler(e.target))
        this.c.on('object:moved',(e)=>handler(e.target))
        this.c.on('object:scaled',(e)=>handler(e.target))
        this.c.on('object:rotated',(e)=>handler(e.target))
        this.c.on('object:skewed',(e)=>handler(e.target))
    }

    /**
     * sets the fill of selected entity
     * @param color {@string} HEX Code for color
     */
    setActiveEntityFill(color:string){
        let klass = this.c.getActiveObject()
        klass.set("fill", color)
        this.c.renderAll()
    }


    /**
     * sets the stroke color of the selected entity
     * @param color {@Link string} HEX color code
     * @returns 
     */
    setActiveEntityStroke(color:string){
        let klass = this.c.getActiveObject()
        if(!klass) return
        klass.set("stroke", color)
        this.c.renderAll()
    }

    /**
     * 
     * @param name {@link string} name of the enity entity
     */
    deleteEntity(name:string){
        const toRemoveEntity = this.c.getObjects().filter(x => x.name === name)
        this.c.remove(toRemoveEntity[0])
    }

    /**
     * removes the seletected Entity. selected entity cant be null or undefined
     */
    deleteActiveEntity(){
        const toRemoveEntity = this.c.getActiveObject()
        if(!toRemoveEntity) return
        this.c.remove(toRemoveEntity)
    }

}

/**
 * Create a singleton instance of {@link SkiiCanvas}
 * @param opt {@link options}
 * @returns {@link SkiiCanvas}
 */
function createCanvas(opt: options):SkiiCanvas{
    if(canvas) return canvas;
    canvas = new SkiiCanvas(opt)
    return canvas
}


export{
    createCanvas,
    SkiiCanvas
}