import './App.css';
import './css/AddImageButton.css'
import {SkiiMainView} from './views/MainView';
import {SizeSelectionView} from './views/sizeSelectionView'
import {
  Routes,
  Route
} from "react-router-dom";
function App() {
  const { innerWidth, innerHeight } = window;
  // const canvasWidth = Math.floor(innerWidth * .65);
  // const canvasHeight = Math.floor(innerHeight * .8);
  return (
    <div className="App">
      <Routes>
        <Route path='/' element = {<SizeSelectionView/>} />
        <Route path='draw' element = {<SkiiMainView width={innerWidth} height={innerHeight} />} />
      </Routes>
    </div>
  );
}

export default App;
