import {SelectedEntityStat} from './SelectedEntityStats'
import {getSelectedEntityAtom} from "../stores/SkiiEntitiesStore"
import {useAtom} from "jotai"
import {Canvas} from "./Canvas"
import { TopBar } from './TopBar'

export interface Props {
    width:number,
    height:number
}


function SkiiMainView(props:Props){
    const [selectedEntity] = useAtom(getSelectedEntityAtom)
    return(
        <div className="main-container">
            <TopBar/>
            <Canvas width={props.width} height={props.height}></Canvas>
            {
                selectedEntity?
                <SelectedEntityStat/>:null
            }
        </div>
    )
}

export {
    SkiiMainView
}