import {Card,Button,mergeRefs} from "@blueprintjs/core"
import {Popover2, Tooltip2} from "@blueprintjs/popover2"
import {getSelectedEntityAtom,deleteEntityAton,activeEntityFillColorAtom, activeEntityStrokeColorAtom} from "../stores/SkiiEntitiesStore"
import {useAtom} from "jotai"
import {HexColorPicker} from "react-colorful"
import "../css/views/SelectedEntityStat.css"
import { CSSProperties,useState } from "react"
/**
 * 
 * @param color hex color code
 * @returns Color Box element
 */
const ColorBox = (color:string|null, onChangeHandeler:((color:string)=>void)) => {
    
    const [bgColor,setBgColor] = useState(color);
    const colorBoxStyle:CSSProperties = {
        width:"2em",
        height:"2em",
        backgroundColor: bgColor as string
    }

    return(
        <div className="selected-entity-stat-color-box">
            <Popover2
                content={<HexColorPicker 
                            color={bgColor as string}
                            onChange={(color:string)=>{
                                setBgColor(color)
                                onChangeHandeler(color)
                            }} />
                        }
                renderTarget={({ isOpen: isPopoverOpen, ref: ref1, ...popoverProps }) => (
                    <Tooltip2
                        disabled={isPopoverOpen}
                        openOnTargetFocus={false}
                        renderTarget={({ isOpen: isTooltipOpen, ref: ref2, ...tooltipProps }) => (
                            <Button
                                minimal={true}
                                outlined={true}
                                style={colorBoxStyle}
                                {...popoverProps}
                                {...tooltipProps}
                                active={isPopoverOpen}
                                elementRef={mergeRefs(ref1, ref2)}
                            />
                        )}
                    />
                )}
            />
            <input type='text' value={color as string}  readOnly={true}/>
        </div>
    )
}

export const SelectedEntityStat = ()=>{
    const [selectedEntity] =useAtom(getSelectedEntityAtom)
    const [,setToRemoveEntity] = useAtom(deleteEntityAton);
    const [,setFillColor] = useAtom(activeEntityFillColorAtom);
    const [,setStrokeColor] = useAtom(activeEntityStrokeColorAtom)
    return(
        <Card
        elevation={2} 
        className="selected-entity-stat">
            <div className="stat-item">
                <span className="stat-item-title">Background</span>
                {ColorBox(selectedEntity?.fill as string,
                    (e)=>{
                        setFillColor(e)
                    })}
            </div>
            <div className="stat-item">
                <span>
                    <b> Height: </b>
                    {(selectedEntity?.height as number *  (selectedEntity?.scaleY as number)).toFixed(2)}
                </span>
                <span>
                    <b> Width: </b>
                    {(selectedEntity?.width as number * (selectedEntity?.scaleX as number)).toFixed(2)}
                </span>
            </div>
           <div className="stat-item">
                <span>
                    <b> Type: </b>
                    {selectedEntity?.type}
                </span>
                <span>
                    <b> Id: </b>
                    {selectedEntity?.name}
                </span>
           </div>
            
            <div className="stat-item">
                <span>
                    <b> Top: </b>
                    {selectedEntity?.top?.toFixed(2)}
                </span>
                <span>
                    <b> Left: </b>
                    {selectedEntity?.left?.toFixed(2)}
                </span>
            </div>

            <div className="stat-item">
                <span>
                    <b> Stroke: </b>
                    {selectedEntity?.stroke}
                    {ColorBox(selectedEntity?.stroke as string,
                         (e)=>{
                            setStrokeColor(e)
                         }
                    )}
                </span>
            </div>
            
            <div className="selected-entity-control">
                <Button icon="eraser"
                    onClick={()=>{
                        if(selectedEntity?.name){
                            setToRemoveEntity(selectedEntity?.name)
                        }
                    }}
                />
            </div>
        </Card>
    )
}