import React from "react"
import {Icon} from "@blueprintjs/core"
export interface AddImageButtonProp {
    onClick: (e:any) =>void
}

export const AddImageButton = (props:AddImageButtonProp) => {
    return (
        <div className="add-image-button">
            <label className="add-image-button-label" htmlFor="add-image-button">
                <Icon icon="media" className="add-image-button-icon"/>
            </label>
            <input type="file" id="add-image-button" onChange={props.onClick} className="add-image-button-file"/>
        </div>
    )
}