import {IconName} from '@blueprintjs/core'
import {atom} from 'jotai'
import {CANVAS_COMMAND} from '../control/skiiCanvas'


export interface Item {
    name:string,
    icon:IconName,
    command:CANVAS_COMMAND
}

const items: Item[] = [
    {name:"circle", icon:"circle", command: CANVAS_COMMAND.CIRCLE},
    {name:"rect", icon:"square", command: CANVAS_COMMAND.SQUARE},
    {name:"draw", icon: "draw", command: CANVAS_COMMAND.DRAW},
]

export function getItems():Item[]{
    return items
}

//jotai Atom
export const selectedItem = atom<string|null>(null)
export const isDrawModeOnAtom = atom<boolean>(false)
const drawCommandAtom = atom<CANVAS_COMMAND>(CANVAS_COMMAND.NONE);
export const drawImageEncodedAtom = atom<string|null>(null)
export const backgroundImageEncodedAtom = atom<string|null>(null)

//derived atoms
export const getDrawCommandAtom = atom((get) => get(drawCommandAtom))
export const setDrawCommandAtom = atom(
    null,
    (get,set,update:CANVAS_COMMAND) => {
        set(drawCommandAtom, ()=> update)
    }
)
