import {Icon} from '@blueprintjs/core'
import '../css/views/TopBar.css'
import {useAtom} from "jotai"
import {setDrawCommandAtom, selectedItem, isDrawModeOnAtom, drawImageEncodedAtom} from '../stores/leftControlContainerStore'
import {CANVAS_COMMAND} from '../control/skiiCanvas'
import {AddImageButton} from "../control/AddImageButton"
export const TopBar = () =>{
    const [,setDrawCommand] = useAtom(setDrawCommandAtom)
    const [isDrawMode, setDrawMode] = useAtom(isDrawModeOnAtom)
    const [,setImageEncodedUrl] = useAtom(drawImageEncodedAtom)
    return(
        <div className="top-bar box-shadow">
            <ul className="top-bar-list">
                <li className="top-bar-item" onClick={(e)=>setDrawMode(false)}>
                    <span className="top-bar-item-icon">
                        <Icon icon="direction-left" ></Icon>
                    </span>
                </li>
                <li className="top-bar-item" onClick={(e)=> setDrawCommand(CANVAS_COMMAND.SQUARE)}>
                    <span className="top-bar-item-icon">
                        <Icon icon="square" />
                    </span>
                </li>
                <li className="top-bar-item" onClick={(e)=> setDrawCommand(CANVAS_COMMAND.CIRCLE)}>
                    <span className="top-bar-item-icon">
                        <Icon icon="circle" />
                    </span>
                </li>
                <li className="top-bar-item">
                    <span className="top-bar-item-icon">
                        <Icon icon="symbol-diamond" ></Icon>
                    </span>
                </li>
                <li className="top-bar-item">
                    <span className="top-bar-item-icon">
                        <Icon icon="arrow-right" />
                    </span>
                </li>
                <li className={"top-bar-item" + (isDrawMode? " active" :"")} onClick={(e)=>setDrawMode(true) } >
                    <span className="top-bar-item-icon">
                        <Icon icon="edit" />
                    </span>
                </li>
                <li className="top-bar-item">
                    <span className="top-bar-item-icon">
                        <Icon icon="new-text-box" />
                    </span>
                </li>
                <li className="top-bar-item">
                    <AddImageButton onClick={(e)=>{
                        let files = (e.target as HTMLInputElement).files;
                        //let fileReader = createFileReaderService()
                        let image = new Image()
                        let fileReader = new FileReader() //fileReader.getSkiiImage(files?.item(0) as File)
                        fileReader.onload = () => {
                            image.src = fileReader.result as string
                            setImageEncodedUrl(image.src)
                        }
    
                        fileReader.readAsDataURL(files?.item(0) as File)
                    }}/>
                </li>
            </ul>           
        </div>
    )
}


